<template>
  <div>
    <img src="@/assets/images/cover.png" alt="">
    <div class="chooseIndex">
      选择人物<br>
      开始探索
    </div>
    <div class="btn1 choose1 choose" @click="turnP1">开始>>></div>
    <div class="btn1 choose2 choose">开始>>></div>
    <div class="btn1 choose3 choose">开始>>></div>
    <div class="btn1 choose4 choose">开始>>></div>
  </div>
</template>

<script>
  export default {
    name:'choose',
    methods:{
      turnP1(){
        this.$router.push({path:'/personIndex1'})
      }
    }
  }
</script>

<style scoped>
  .chooseIndex{
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
    width: 5rem;
    height: 3rem;
    text-align: right;
    font-weight: 300;
    font-size: 1rem;
    padding-bottom: 0;
    color: #ffffff;
    margin-top:20px;
  }
  .choose{
    font-weight: bold;
    line-height:3rem;
    text-align:center;
  }
  .choose1{
    right: 1.5rem;
    top: 3rem;
    width: 8rem;
    height: 3rem;
    animation-name: btn_animation4;
  }
  .choose2{
    left: 2.5rem;
    top: 3rem;
    width: 8rem;
    height: 3rem;
    text-align: left;
    animation-name: btn_animation7;
  }
  .choose3{
    right: 3rem;
    top: 3rem;
    width: 8rem;
    height: 3rem;
    text-align: right;
    animation-name: btn_animation6;
  }
  .choose4{
    left: 5rem;
    top: 15.5rem;
    width: 8rem;
    height: 3rem;
    text-align: left;
    animation-name: btn_animation5;
  }
</style>



